.header {
  padding: 25px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__logo {
    span {
      font-size: 30px;
      line-height: 120%;
      font-weight: 700;
      color: #8A005C;
      text-transform: uppercase;
    }
  }
  &__user {
    display: flex;
    align-items: center;
    gap: 14px;
    span {
      font-size: 24px;
      line-height: 120%;
    }
    i {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 16px 0;
    &__logo {
      span {
        font-size: 24px;
      }
    }
    &__user {
      gap: 8px;
      span {
        font-size: 18px;
      }
      i {
        width: 32px;
        height: 32px;
      }
    }
  }
}