.footer {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    border-top: 1px solid rgba(0, 0, 0, 0.16);
    padding: 25px 0;
  }
  &__logo {
    span {
      font-size: 30px;
      line-height: 120%;
      font-weight: 700;
      color: #8A005C;
      text-transform: uppercase;
    }
  }
  &__phone {
    font-size: 22px;
    line-height: 120%;
    &:hover {
      color: #8A005C;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__inner {
      padding: 16px 0;
    }
    &__logo {
      span {
        font-size: 24px;
      }
    }
    &__phone {
      font-size: 18px;
    }
  }
}