.main {
  &__inner {
    padding: 25px 0 100px;
  }
  &__tabs-head {
    display: flex;
    align-items: stretch;
    gap: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.16);
    padding-bottom: 10px;
  }
  &__tab {
    font-size: 24px;
    line-height: 120%;
  }
  .tab,
  .tab2 {
    position: relative;
    cursor: pointer;
    transition: all 0.3s;
    &:after {
      content: '';
      position: absolute;
      z-index: 2;
      left: 0;
      bottom: -12px;
      width: 100%;
      height: 3px;
      background: #8A005C;
      border-radius: 3px;
      opacity: 0;
    }
  }
  .tab--active,
  .tab2--active {
    &:after {
      opacity: 1;
    }
  }
  &__tabs-body {
    margin-top: 50px;
  }
  &__title {
    font-size: 32px;
    line-height: 120%;
    font-weight: 600;
  }
  .tab__content,
  .tab__content2 {
    display: none;
    &--active {
      display: block;
    }
  }
}
.marketplaces {
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 25px / 2));
    gap: 25px;
    margin-top: 25px;
  }
  &__tabs-head {
    display: flex;
    align-items: stretch;
    gap: 50px;
  }
  &__tab {
    font-size: 22px;
    line-height: 120%;
    padding-bottom: 10px;
  }
  .tab2 {
    &:nth-child(1) {
      padding-left: 20px;
      &:after {
        bottom: -100%;
        height: 38px;
        background: transparent;
        border: 3px solid #8A005C;
        border-right: unset;
        border-bottom: unset;
        border-radius: 20px 0 0 0;
      }
    }
    &:after {
      bottom: -2px;
    }
  }
  .tab2--active {
    color: #8A005C;
  }
  .tab__content2 {
    position: relative;
    background: #fff;
    border: 1px solid rgba(0, 0, 0, 0.20);
    border-radius: 20px;
    padding: 32px 20px;
    &:after {
      content: '';
      position: absolute;
      z-index: 3;
      top: 1px;
      left: 0;
      width: 200px;
      height: 38px;
      background: transparent;
      border-radius: 20px 0 0 0;
      pointer-events: none;
    }
  }
  .search {
    position: relative;
    z-index: 4;
  }
  &__subtitle {
    background: #8A005C;
    border-radius: 10px;
    padding: 16px;
    font-size: 22px;
    line-height: 120%;
    font-weight: 600;
    color: #fff;
    margin-top: 20px;
  }
  &__subitems {
    display: grid;
    grid-template-columns: 100%;
  }
  &__subitem {
    &-show {
      display: flex;
      align-items: center;
      gap: 15px;
      cursor: pointer;
      padding: 12px 16px;
    }
    .item-show--active {
      background: #F3F3F3;
      .marketplaces__subitem-btn {
        transform: rotate(90deg);
      }
    }
    &-btn {
      width: 9px;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
    &-title {
      font-size: 22px;
      line-height: 120%;
    }
    &-hidden {
      position: relative;
      display: none;
      padding-left: 50px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 24px;
        width: 1px;
        height: 100%;
        background: rgba(0, 0, 0, 0.16);
        pointer-events: none;
      }
    }
    .level2 {
      position: relative;
      margin-top: 10px;
      .item-show {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: -24px;
          transform: translate(0, -50%);
          width: 24px;
          height: 1px;
          background: rgba(0, 0, 0, 0.16);
          pointer-events: none;
        }
      }
    }
  }
  &__inputs {
    margin-top: 10px;
  }
  &__input {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 16px;
      transform: translate(0, -50%);
      width: 18px;
      height: 18px;
      background-image: url("../img/editing.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    &-title {
      position: absolute;
      top: 50%;
      left: 16px;
      transform: translate(0, -50%);
      font-size: 22px;
      line-height: 120%;
    }
    input {
      position: relative;
      z-index: 2;
      width: 100%;
      height: 50px;
      text-align: right;
      background: transparent;
      border: 1px solid transparent;
      border-radius: 8px;
      padding: 0 44px 0 134px;
      font-size: 22px;
      line-height: 120%;
      &::placeholder {
        color: #171616;
      }
      &:focus {
        border-color: #8A005C;
      }
    }
  }
  .select__wrap {
    position: relative;
    z-index: 3;
  }
  .select__wrap--active {
    .select__list-default {
      border-color: #8A005C;
    }
    .select-title i {
      transform: rotate(-180deg);
    }
    .select__list {
      opacity: 1;
      pointer-events: auto;
    }
  }
  .select__list-default {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    border: 1px solid transparent;
    border-radius: 8px;
    padding: 0 16px;
    cursor: pointer;
  }
  .select-title {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 22px;
    line-height: 120%;
    i {
      width: 16px;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
  }
  .select__current {
    font-size: 22px;
    line-height: 120%;
    input {
      display: none;
    }
    span {
      display: none;
    }
  }
  .select__list {
    position: absolute;
    z-index: 2;
    top: calc(100% + 5px);
    width: 320px;
    background: #fff;
    box-shadow: 0px 3px 7px 0px rgba(138, 138, 138, 0.1),0px 12px 12px 0px rgba(138, 138, 138, 0.09),0px 28px 17px 0px rgba(138, 138, 138, 0.05),0px 49px 20px 0px rgba(138, 138, 138, 0.01),0px 77px 22px 0px rgba(138, 138, 138, 0),0px -2px 15.2px 0px rgba(0, 0, 0, 0.06);
    border-radius: 10px;
    padding: 16px 20px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &:after {
      content: '';
      position: absolute;
      top: -20px;
      left: 22px;
      border: 10px solid transparent;
      border-bottom: 14px solid #fff;
    }
  }
  .categories-sidebar__checkboxes {
    height: 245px;
    margin-top: 0;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 5px;
    }
    &::-webkit-scrollbar-track {
      background: transparent;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #D9D9D9;
      border-radius: 3px;
    }
  }
  .categories-sidebar__checkbox {
    input:checked ~ label {
      &:after {
        border-color: rgba(0,0,0,.2);
      }
    }
    label {
      display: flex;
      align-items: center;
      gap: 10px;
      &:after {
        top: 0;
        background: transparent;
        border-color: rgba(0,0,0,.2);
        border-radius: 4px;
      }
      span {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        margin-top: -2px;
      }
    }
  }
  .select-btn {
    width: 100%;
    height: 50px;
    padding: 5px 10px;
    margin-top: 20px;
    span {
      font-size: 22px;
    }
  }
  .breakpoints {
    margin-top: 30px;
  }
  &__btn {
    height: 67px;
    padding: 5px 24px;
    margin: 50px 0 0 auto;
    span {
      font-size: 22px;
    }
  }
}
.categories {
  display: flex;
  justify-content: space-between;
  align-items: start;
  gap: 20px;
  margin-top: 30px;
  &__left {
    width: 415px;
  }
  &__right {
    width: calc(100% - 440px);
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 40px 20px;
    margin-top: 30px;
  }
  .pagination {
    margin-top: 50px;
  }
}
.categories-sidebar {
  display: grid;
  grid-template-columns: 100%;
  gap: 30px;
  &__title {
    background: #8A005C;
    border-radius: 10px;
    padding: 15px 25px;
    font-size: 24px;
    line-height: 120%;
    font-weight: 600;
    color: #fff;
  }
  &__checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 16px;
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        border-width: 6px;
        border-color: #8A005C;
      }
    }
    label {
      position: relative;
      font-size: 22px;
      line-height: 120%;
      padding-left: 30px;
      &:after {
        content: '';
        position: absolute;
        top: 3px;
        left: 0;
        width: 20px;
        height: 20px;
        background: rgba(138, 0, 92, 0.06);
        border: 1px solid rgba(138, 0, 92, 0.26);
        border-radius: 50%;
      }
    }
  }
  &__checkbox2 {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      background: #8A005C;
      &:after {
        left: calc(100% - 31px);
      }
    }
    label {
      position: relative;
      display: block;
      width: 61px;
      height: 34px;
      background: #D9D9D9;
      border-radius: 30px;
      transition: all 0.3s;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 2px;
        transform: translate(0, -50%);
        width: 29px;
        height: 29px;
        background: #fff;
        border-radius: 50%;
        transition: all 0.5s;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 16px;
  }
  &__items2 {
    gap: 20px;
  }
  &__item {
    &-show {
      display: flex;
      align-items: center;
      gap: 10px;
      cursor: pointer;
    }
    .item-show--active {
      .categories-sidebar__item-btn,
      .categories-sidebar__item-btn2 {
        transform: rotate(-180deg);
      }
    }
    &-show2 {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.14);
      padding-bottom: 15px;
      cursor: pointer;
    }
    &-btn2 {
      width: 25px;
      height: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #D9D9D9;
      border-radius: 50%;
      transition: all 0.3s;
    }
    &-btn {
      width: 16px;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
    &-title {
      font-size: 22px;
      line-height: 120%;
    }
    &-hidden {
      position: relative;
      display: none;
      grid-template-columns: 100%;
      gap: 10px;
      padding-left: 40px;
      margin-top: 10px;
      &:after {
        content: '';
        position: absolute;
        top: -10px;
        left: 8px;
        width: 1px;
        height: 100%;
        background: rgba(0, 0, 0, 0.16);
      }
    }
    &-hidden2 {
      display: none;
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
      margin-top: 16px;
      li a {
        position: relative;
        width: 100%;
        font-size: 22px;
        line-height: 120%;
        &:after {
          content: '';
          position: absolute;
          top: 10px;
          left: -33px;
          width: 25px;
          height: 1px;
          background: rgba(0, 0, 0, 0.16);
        }
        &:hover {
          color: #8A005C;
        }
      }
    }
    .level2 {
      position: relative;
      &:not(:first-child) {
        margin-top: 10px;
      }
      &:after {
        content: '';
        position: absolute;
        top: 15px;
        left: -33px;
        width: 25px;
        height: 1px;
        background: rgba(0, 0, 0, 0.16);
      }
    }
  }
  &__inputs {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 10px / 2));
    gap: 10px;
    margin-top: 16px;
  }
  &__input {
    position: relative;
    &:nth-child(1) {
      input {
        padding-top: 32px;
      }
    }
    &:nth-child(2) {
      input {
        padding-bottom: 32px;
      }
      span {
        top: unset;
        bottom: 12px;
      }
    }
    input {
      width: 100%;
      height: 80px;
      border: 1px solid rgba(0, 0, 0, 0.12);
      border-radius: 10px;
      padding: 0 16px;
      font-size: 22px;
      line-height: 120%;
      margin: 0;
    }
    span {
      position: absolute;
      top: 12px;
      left: 16px;
      font-size: 20px;
      line-height: 120%;
      color: rgba(0, 0, 0, 0.35);
      pointer-events: none;
    }
  }
  .colors-checkboxes {
    .categories-sidebar__checkbox {
      &:nth-child(7) ~ .categories-sidebar__checkbox {
        display: none;
      }
      span {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        margin-top: -2px;
      }
      label {
        display: flex;
        align-items: center;
        gap: 10px;
        &:after {
          top: 1px;
          background: transparent;
          border-color: rgba(0, 0, 0, 0.2);
          border-radius: 4px;
        }
      }
    }
  }
  .colors-checkboxes--active {
    .categories-sidebar__checkbox {
      display: block !important;
    }
  }
  .all-show__btn {
    margin-top: 16px;
    cursor: pointer;
    span {
      font-size: 22px;
      line-height: 120%;
      color: #8A005C;
      text-decoration: underline;
      text-underline-offset: 2px;
    }
    span:nth-child(2) {
      display: none;
    }
    &--active {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: block;
      }
    }
  }
}
.categories-card {
  &__slider {
    position: relative;
    padding-bottom: 20px;
  }
  .swiper-slide {
    position: relative;
    padding-bottom: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  .swiper-pagination {
    bottom: 0;
    &-bullet {
      width: 8.7px;
      height: 8.7px;
      background: #D9D9D9;
      opacity: 1;
    }
    &-bullet-active {
      background: #979797;
    }
  }
  &__desc {
    margin-top: 20px;
  }
  &__price {
    font-size: 28px;
    line-height: 120%;
    font-weight: 600;
  }
  &__text {
    margin-top: 5px;
    p {
      font-size: 22px;
      line-height: 120%;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:3;
      -webkit-box-orient: vertical;
    }
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 16px;
    margin-top: 10px;
  }
  .star {
    display: flex;
    align-items: center;
    gap: 5px;
    i {
      width: 16px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 120%;
    }
  }
  &__info {
    font-size: 18px;
    line-height: 120%;
  }
  &__shop {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 20px;
    i {
      width: 26px;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 120%;
      color: rgba(0, 0, 0, 0.3);
      border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    }
  }
}
.selections {
  margin-top: 50px;
  &__title {
    font-size: 28px;
    line-height: 120%;
    font-weight: 600;
  }
  .search {
    display: flex;
    align-items: stretch;
    gap: 16px;
    margin-top: 20px;
    input {
      padding-right: 16px;
    }
  }
  .search-btn {
    position: static;
    transform: unset;
    width: auto;
    height: 60px;
    flex-shrink: 0;
    padding: 5px 20px;
    span {
      font-size: 18px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 40px;
  }
  &__item {
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 16px;
      background: #F3F3F3;
      border-radius: 20px;
      padding: 24px 20px;
      cursor: pointer;
    }
    .item-show--active {
      .selections__item-btn {
        transform: rotate(-180deg);
        background: #8A005C;
      }
    }
    &-btn {
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #D9D9D9;
      border-radius: 50%;
      transition: all 0.3s;
      img {
        max-width: 90%;
      }
    }
    &-hidden {
      margin-top: 30px;
    }
    &-group {
      display: flex;
      align-items: center;
      gap: 20px;
    }
    .select-all--active {
      label {
        &:after {
          background-image: url("../img/check.svg");
          background-color: #8A005C;
          border-color: #8A005C;
        }
      }
    }
    .selections-checkbox {
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
      }
      label {
        position: relative;
        font-size: 22px;
        line-height: 120%;
        padding-left: 30px;
        &:after {
          content: '';
          position: absolute;
          top: 3px;
          left: 0;
          width: 20px;
          height: 20px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: auto;
          border: 1px solid rgba(0, 0, 0, 0.5);
          border-radius: 5px;
        }
      }
    }
    .selected-ones {
      font-size: 22px;
      line-height: 120%;
      font-weight: 600;
      color: #EC2D0B;
      cursor: pointer;
    }
  }
  &__subitems {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 20px;
  }
  &__subitem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    border-radius: 20px;
    padding: 40px;
    &:nth-child(odd) {
      background: #F3F3F3;
    }
    &--active {
      background: rgba(138, 0, 92, 0.1) !important;
      .selections-checkbox {
        label {
          &:after {
            background-image: url("../img/check.svg");
            background-color: #8A005C;
            border-color: #8A005C;
          }
        }
      }
    }
    &-group {
      display: flex;
      align-items: center;
      gap: 40px;
    }
    &-date {
      font-size: 22px;
      line-height: 120%;
    }
    &-delete {
      width: 21px;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
  }
}
.settings {
  &__tabs-head {
    display: flex;
    justify-content: center;
    align-items: stretch;
    gap: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.16);
    padding-bottom: 10px;
  }
  &__tab {
    display: flex;
    align-items: center;
    font-size: 22px;
    line-height: 120%;
  }
  //.container2 {
  //  max-width: 2560px;
  //}
  &__tabs-body {
    margin-top: 25px;
  }
  &__table-select {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-top: 32px;
  }
  .select-title {
    font-size: 20px;
    line-height: 120%;
  }
  .nice-select {
    width: auto;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    border-radius: 8px;
    padding: 4px 26px 4px 10px;
    font-size: 20px;
    line-height: 120%;
    &:after {
      top: 50%;
      right: 10px;
      transform: translate(0, -50%);
      width: 12px;
      height: 7px;
      background-image: url("../img/arrow5.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      background-color: transparent;
      transition: all 0.3s;
    }
    .list {
      li {
        font-size: 20px;
      }
    }
  }
}
.settings__table {
  //max-width: 2124px;
  width: 100%;
  border-collapse: collapse;
  margin: 0 auto;
  thead {
    tr {
      background: #F3F3F3;
      border-top: 1px solid rgba(0, 0, 0, 0.16);
      border-bottom: 1px solid rgba(0, 0, 0, 0.16);
    }
  }
  tbody {
    tr {
