.registration {
  padding: 364px 0;
  &__form {
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    margin-top: 40px;
    &-wrap {
      max-width: 657px;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      border: 1px solid rgba(0, 0, 0, 0.33);
      border-radius: 20px;
      padding: 50px;
      margin: auto;
    }
    &-group {
      input {
        height: 89px;
        padding: 0 20px;
      }
    }
    &-btn {
      width: 100%;
      height: 89px;
      margin-top: 10px;
      span {
        font-size: 24px;
      }
    }
  }
  &__logo {
    font-size: 54px;
    line-height: 120%;
    font-weight: 700;
    color: #8A005C;
    text-transform: uppercase;
  }
  &__title {
    font-size: 36px;
    line-height: 120%;
    font-weight: 600;
    color: #171616;
    margin-top: 50px;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    color: #383838;
    margin-top: 15px;
  }
}

@media screen and (max-width: 992px) {
  .registration {
    padding: 100px 0;
    &__form {
      gap: 16px;
      margin-top: 16px;
      &-wrap {
        max-width: 500px;
        padding: 24px 16px;
      }
      &-group {
        input {
          height: 60px;
          padding: 0 16px;
          font-size: 16px;
        }
      }
      &-btn {
        height: 60px;
        span {
          font-size: 16px;
        }
      }
    }
    &__logo {
      font-size: 32px;
    }
    &__title {
      font-size: 24px;
      margin-top: 32px;
    }
    &__subtitle {
      font-size: 16px;
      margin-top: 16px;
    }
  }
}