.btn-vinous {
  background: #8A005C !important;
  border-radius: 10px;
  &:hover {
    background: #780050 !important;
  }
  span {
    line-height: 120%;
    font-weight: 600;
    color: #fff;
  }
}
.form {
  &-group {
    input {
      width: 100%;
      background: #F3F3F3;
      border: 2px solid transparent;
      border-radius: 10px;
      padding: 0 16px;
      font-size: 24px;
      line-height: 120%;
      &::placeholder {
        color: #9C9C9C;
      }
      &:focus {
        background: transparent;
        border-color: #8A005C;
        &::placeholder {
          color: transparent;
        }
      }

    }
  }
}
.search {
  position: relative;
  input {
    width: 100%;
    height: 60px;
    background: transparent;
    border: 1px solid #CACACA;
    border-radius: 10px;
    padding: 0 60px 0 16px;
    font-size: 22px;
    line-height: 120%;
    &::placeholder {
      color: rgba(0, 0, 0, 0.50);
    }
    &:focus {
      border: 1px solid rgba(0, 0, 0, 0.8);
    }
  }
  &-btn {
    position: absolute;
    z-index: 2;
    top: 50%;
    right: 16px;
    transform: translate(0, -50%);
    width: 28px;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  &__prev,
  &__next {
    font-size: 18px;
    line-height: 120%;
    &:hover {
      color: rgba(0, 0, 0, 0.5);
    }
  }
  &__items {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__item {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    &--active {
      background: rgba(138, 0, 92, 0.1);
      color: #8A005C;
    }
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0,0,0,.19);
  padding: 0 15px;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active {
    opacity: 1;
    pointer-events: unset
  }
  @media screen and (min-width: 992px) {
    & {
      text-align:center;
    }
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%
    }
    .modal__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog {
    position: relative
  }
  @media screen and (max-width: 992px) {
    .modal__dialog {
      margin: 50px auto;
      max-width: calc(100% - 30px)
    }
  }
  .download-modal {
    max-width: 362px;
    width: 100%;
    background: #fff;
    border-radius: 10px;
    padding: 30px 36px;
  }
  .parameters-modal {
    max-width: 1010px;
    width: 100%;
    background: #fff;
    border-radius: 10px;
    padding: 30px 45px;
    .modal__title {
      text-align: center;
    }
  }
  .form-modal {
    max-width: 500px;
    width: 100%;
    background: #fff;
    border-radius: 20px;
    padding: 36px;
    .modal__title {
      text-align: center;
    }
  }
  &__title {
    font-size: 24px;
    line-height: 120%;
    font-weight: 600;
  }
  .download-modal__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 16px / 2));
    gap: 16px;
    margin-top: 30px;
  }
  .download-modal__btn {
    height: 58px;
    padding: 5px;
    span {
      font-size: 18px;
    }
  }
  .download-modal__btn2 {
    background: #EDEDED;
    border-radius: 10px;
    span {
      line-height: 120%;
      font-weight: 600;
    }
  }
  .parameters-modal__top {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 30px / 4));
    gap: 10px;
    background: #8A005C;
    border-radius: 10px;
    padding: 16px;
    margin-top: 20px;
    span {
      font-size: 20px;
      line-height: 120%;
      font-weight: 600;
      color: #fff;
    }
  }
  .parameters-modal__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 30px / 4));
    gap: 10px;
    margin-top: 20px;
  }
  .parameters-modal__item {
    display: flex;
    flex-direction: column;
    background: #F3F3F3;
    border-radius: 10px;
    padding: 13px 16px;
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: rgba(0, 0, 0, 0.6);
      margin-bottom: auto;
    }
    &-number {
      font-size: 20px;
      line-height: 120%;
      font-weight: 600;
      color: #8A005C;
      margin-top: 10px;
    }
  }
  .parameters-modal__btn {
    height: 64px;
    padding: 5px 24px;
    margin: 30px auto 0;
    span {
      font-size: 20px;
    }
  }
  .form-modal__subtitle {
    text-align: center;
    font-size: 22px;
    line-height: 120%;
    margin-top: 10px;
  }
  .form-modal__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 25px;
    &-group input {
      height: 75px;
      font-size: 22px;
    }
    &-btn {
      height: 75px;
      margin-top: 20px;
      span {
        font-size: 22px;
      }
    }
  }
}
.pagination2 {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;
  &__prev,
  &__next {
    height: 90px;
    display: flex;
    align-items: center;
    gap: 30px;
    background: #FAF4F8;
    border: 1px solid #8A005C;
    border-radius: 40px;
    padding: 5px 30px;
    i {
      width: 35px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 26px;
      line-height: 120%;
      font-weight: 600;
    }
  }
  .disabled {
    background: transparent;
    border: 1px solid #CACACA;
    i svg {
      path {
        fill: #838383;
      }
    }
    span {
      color: #838383;
    }
  }
  &__items {
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    border: 1px solid #CACACA;
    border-radius: 40px;
    padding: 5px 30px;
  }
  &__item {
    width: 58px;
    height: 58px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid transparent;
    border-radius: 20px;
    svg {
      max-width: 32px;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
    &:hover {
      background: #F3F3F3;
    }
    &1:hover {
      svg path {
        fill: #EC2D0B;
      }
    }
    &1:active {
      background: rgba(236, 45, 11, 0.12);
      border-color: #EC2D0B;
      svg path {
        fill: #EC2D0B;
      }
    }
    &2:hover {
      svg path {
        fill: #8A005C;
      }
    }
    &2:active {
      background: #FAF4F8;
      border-color: #8A005C;
      svg path {
        fill: #8A005C;
      }
    }
    &3:hover {
      svg path {
        fill: #8A005C;
      }
    }
    &3:active {
      background: #FAF4F8;
      border-color: #8A005C;
      svg path {
        fill: #8A005C;
      }
    }
    &4:hover {
      svg path {
        fill: #30B00F;
      }
    }
    &4:active {
      background: #DCF4D6;
      border-color: #30B00F;
      svg path {
        fill: #30B00F;
      }
    }
  }
}
.nice-select {
  .list {
    width: auto !important;
  }
}
.breakpoints {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  &__item {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 22px;
    line-height: 120%;
    color: rgba(0, 0, 0, 0.6);
    &--active {
      color: #000;
    }
    &:hover {
      color: #000;
    }
    i {
      max-width: 14px;
      svg {
        max-width: 100%;
        width: auto;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
  }
}


@media screen and (max-width: 1200px) {
  .modal {
    .parameters-modal {
      max-width: 900px;
    }
  }
}

@media screen and (max-width: 992px) {
  .search {
    input {
      font-size: 16px;
    }
  }
  .modal {
    &__title {
      font-size: 20px;
    }
    .download-modal__btns {
      margin-top: 16px;
    }
    .download-modal__btn {
      height: 45px;
      span {
        font-size: 16px;
      }
    }
    .parameters-modal,
    .form-modal {
      padding: 30px 24px;
    }
    .parameters-modal__top {
      span {
        font-size: 16px;
      }
    }
    .parameters-modal__item {
      &-number {
        font-size: 16px;
      }
    }
    .parameters-modal__btn {
      height: 56px;
      span {
        font-size: 18px;
      }
    }
    .form-modal__subtitle {
      font-size: 16px;
    }
    .form-modal__form {
      &-group {
        input {
          height: 60px;
          padding: 0 16px;
          font-size: 16px;
        }
      }
      &-btn {
        height: 60px;
        span {
          font-size: 16px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .modal {
    .parameters-modal__items {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .modal {
    .parameters-modal__top {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
  }
}

@media screen and (max-width: 359px) {
  .modal {
    .parameters-modal__top {
      grid-template-columns: 100%;
    }
    .parameters-modal__items {
      grid-template-columns: 100%;
    }
  }
}